p {
    font-size: 2rem;
}

.servics__container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #503047;
    margin: 4rem 8rem;
    border-radius: 1rem;
    padding: 6rem;
    gap: 3rem;
}

.servics__container h1 {
    color: white;
    font-size: 4rem;
}

.services {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}

.items {
    background-color: white;
    padding: .3rem 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2.4rem;
    border-radius: 1rem;
    cursor: pointer;
}

.items img {
    height: 3rem;
}