.button_section {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 8rem;
    gap: 1.5rem;
}

.button_section button {
    font-size: 1.7rem;
    border: none;
    padding: 1rem;
    color: white;
    background-color: #727272;
    border-radius: 1rem;

}

.button_section p {
    font-size: 1.8rem;
}