.sticky {
   position: fixed;
   top: 0;
   width: 100%;
}

.navbar__container {
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 3rem;
   box-shadow: 5px 5px 5px #DADBDC;
   margin-top: -4rem;
   background-color: white;

}

.logo img {
   height: 20rem;
   width: auto;
   margin-top: 0rem;
}


.search {
   display: flex;
   align-items: flex-start;
   justify-content: center;
   width: 30%;
}

.search input {

   padding: .4rem;
   border: none;
   border: 1px solid rgb(206, 199, 199);
   text-align: center;
   box-shadow: 0px 0px 2px #888888;
   width: 100%;
   height: auto;
   font-size: 1.7rem;



}

.search img {
   background-color: #797578;
   padding: 0rem;
   height: 3.5rem;
   width: auto;
   cursor: pointer;


}

.link__container {
   display: flex;
   align-items: center;
   justify-content: center;
   flex-direction: column;
   gap: 1rem;

   margin-left: 16rem;
}

.lower__links ul {
   display: flex;
   align-items: center;
   justify-content: center;
   flex-wrap: wrap;
   gap: 2rem;

}

.lower__links ul li {
   list-style: none;

}

.lower__links ul li a {
   text-decoration: none;
   font-size: 1.7rem;
   color: rgb(156, 153, 153);

}


.active {
   color: #503047;
}

.upper__btn {
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 1rem;
}

.btn1 {
   border: none;
   background-color: #503047;
   color: white;
   padding: 1rem;
   font-size: 1.5rem;
   border-radius: .8rem;
   cursor: pointer;

}