.event_section {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 2rem;

}



.event__container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.event__container--items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

}

.event__container--items img {
    height: 25rem;
    width: auto;
}

.btn {
    font-size: 1.5rem;
    background-color: #503047;
    color: white;
    padding: 1rem 2rem;
    border-radius: 1rem;

}

.btn:hover {
    background-color: transparent;
    color: black;
    border: #503047 1px solid;
}